//
// Buttons
// --------------------------------------------------


// Overriding button variant colors
// 
// Solid variant

@each $color, $value in map-remove($theme-colors, 'light') {
  .btn-#{$color} {
    color: $btn-dark-color;

    &:hover,
    &:focus,
    &:active,
    &.active,
    &.show {
      background-color: darken($value, 7.5%);
      border-color: darken($value, 7.5%);
      color: $btn-dark-color;
    }

    &:disabled,
    &.disabled {
      color: $btn-dark-color;
    }
  }
}

.btn-secondary {
  &,
  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    color: $gray-700;
  }

  &:disabled,
  &.disabled {
    color: $btn-light-color;
  }
}

.btn-light {
  border-color: transparent;
  background-color: $light;
  color: $btn-light-color;

  &:hover,
  &:focus,
  &:active,
  &.active {
    border-color: $light;
    background-color: $light;
  }
}


// Outline variant

[class^='btn-outline-'],
[class*=' btn-outline-'] {
  &:hover, &:active,
  &.active, &.dropdown-toggle.show {
    color: $btn-dark-color;
  }
}

@each $color, $value in $border-colors {
  .btn-outline-#{$color} {
    border-color: $value;
  }
}

.btn-outline-secondary {
  border-color: $gray-400;
  color: $btn-light-color;

  &:hover, &:focus, &:active,
  &.active, &.show {
    border-color: $gray-500;
    background-color: transparent;
    color: $gray-800 !important;
  }
}

.btn-outline-light {
  border-color: rgba($light, .35);
  &:hover, &:focus, &:active,
  &.active, &.show {
    background-color: $light;
    color: $gray-800;
  }
}

.btn-check + .btn-outline-primary {
  &:hover, &:active {
    border-color: rgba($primary, .35);
    background-color: transparent;
    color: $primary;
  }
}
.btn-check:checked + .btn-outline-primary {
  border-color: transparent;
  background-color: rgba($primary, .08);
  color: $primary;
  &:active {
    color: $primary;
  }
}


// Translucent variant

[class^='btn-translucent-'],
[class*=' btn-translucent-'] {
  &:hover, &:focus, &:active,
  &.active, &.show {
    color: $btn-dark-color;
  }
}

@each $color, $value in $theme-faded-colors {
  .btn-translucent-#{$color} {
    background-color: $value;
  }
}

@each $color, $value in map-remove($theme-colors, 'secondary', 'light') {
  .btn-translucent-#{$color} {
    color: $value;
    &:hover, &:focus, &:active,
    &.active, &.show {
      background-color: $value;
    }
  }
}

.btn-translucent-light {
  color: $light;

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    background-color: $light;
    color: $gray-900;
  }
}


// Link button

.btn-link {
  font-weight: $btn-font-weight;
  text-decoration: $btn-link-decoration;
  
  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    text-decoration: $btn-link-hover-decoration;
  }
}


// Gradient button

.btn-gradient {
  background-color: $primary;
  background-image: $gradient;

  &,
  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    color: $btn-dark-color;
  }
}


// Icons
// 
// Alignment

.btn {
  > [class^='ai-'],
  > [class*=' ai-'] {
    margin-top: -.125rem;
    vertical-align: middle;
    @include font-size($btn-icon-size);
  }
}

// Icon button

.btn-icon { padding: $btn-padding-y ($btn-padding-y * 1.4); }


// Market buttons

.btn-market {
  display: inline-block;
  padding: $btn-market-padding-y $btn-market-padding-x;
  padding-left: $btn-market-padding-x * 1.5 + $btn-market-icon-size;
  @include border-radius($btn-market-border-radius);
  background: {
    position: center left .75rem;
    color: $btn-market-bg;
    size: $btn-market-icon-size $btn-market-icon-size;
    repeat: no-repeat;
  }
  text-decoration: none;
  white-space: nowrap;

  .btn-market-title {
    display: block;
    color: $btn-market-title-color;
    @include font-size($btn-market-title-font-size);
  }

  .btn-market-subtitle {
    display: block;
    margin-bottom: -.125rem;
    color: $btn-market-subtitle-color;
    @include font-size($btn-market-subtitle-font-size);
  }

  &:hover { text-decoration: none; }
  &.btn-outline {
    transition: $btn-market-outline-transition;
    border: $border-width solid $btn-market-outline-border-color;
    background-color: transparent;
    &:hover {
      background-color: $btn-market-outline-hover-bg;
    }
  }
}

// App Store
.btn-apple {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

// Google Play
.btn-google {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

// Windows Store
.btn-windows {
  background-image: url(data:image/svg+xml;utf8;base64,);
}

// Amazom Market
.btn-amazon {
  background-image: url(data:image/png;base64,);
}


// Scroll to top button

.btn-scroll-top {
  display: block;
  position: fixed;
  right: -($btn-scroll-top-size * 1.5);
  bottom: $spacer * 1.25;
  width: $btn-scroll-top-size;
  height: $btn-scroll-top-size;
  transition: $btn-scroll-top-transition;
  @include border-radius($btn-border-radius);
  background-color: $btn-scroll-top-bg;
  color: $btn-scroll-top-color;
  text-align: center;
  opacity: 0;
  z-index: $zindex-btn-scroll-top;

  > .btn-scroll-top-icon {
    font: {
      size: $btn-scroll-top-icon-font-size;
      weight: bold;
    }
    line-height: $btn-scroll-top-size - .125rem;
  }

  .btn-scroll-top-tooltip {
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    transition: opacity .25s ease-in-out, visibility .25s ease-in-out;
    visibility: hidden;
    opacity: 0;
  }

  &:hover {
    background-color: $btn-scroll-top-hover-bg;
    color: $btn-scroll-top-hover-color;
    .btn-scroll-top-tooltip {
      visibility: visible;
      opacity: 1;
    }
  }

  &.show {
    right: $spacer * 1.25;
    opacity: 1;
  }

  @include media-breakpoint-down(sm) {
    width: $btn-scroll-top-size * .833;
    height: $btn-scroll-top-size * .833;
    bottom: $spacer;
    > .btn-scroll-top-icon {
      line-height: $btn-scroll-top-size * .833;
    }
    &.show { right: $spacer; }
  }
}


// Like / Dislike buttons

.btn-like,
.btn-dislike {
  display: inline-block;
  padding: 0;
  padding-left: .625rem;
  transition: $nav-link-transition;
  border: 0;
  background: 0;
  color: lighten($gray-600, 3%);
  @include font-size($font-size-sm);
  text-decoration: none !important;
  white-space: nowrap;
  vertical-align: middle;

  &::before {
    display: inline-block;
    margin-right: .375rem;
    font: {
      family: $icons-font-family;
      size: 1.25em;
    }
    vertical-align: middle;
  }

  &:first-child {
    padding: {
      right: .625rem;
      left: 0;
    }
    border-right: $border-width solid darken($border-color, 2%);
  }

  &:focus { outline: none; }
  &:hover { color: $gray-800; }
}

.btn-like::before {
  margin-top: -.325rem;
  color: $success;
  content: $ai-thumbs-up;
}

.btn-dislike::before {
  color: $danger;
  content: $ai-thumbs-down;
}
